.calendar {
  margin-left: 50px;
  display: flex;
  flex-direction: column; }
  .calendar .row {
    display: flex; }
  .calendar .pushLeft {
    display: flex;
    align-items: flex-start; }

.calendar-container {
  margin: 0 auto;
  margin-top: 30px;
  padding: 20px;
  width: 840px; }

.day-nr {
  margin: 5px 5px 0px 0px;
  text-align: right;
  font-size: 10px; }

.date-grid {
  display: grid;
  justify-items: center;
  grid-row-gap: 0;
  width: 130px;
  grid-template-columns: repeat(7, 1fr); }

.day-of-week {
  display: grid;
  margin-bottom: 20px;
  color: #2E3835;
  align-items: flex-start;
  grid-gap: 1px;
  grid-template-columns: repeat(7, 1fr); }
  .day-of-week .day {
    box-sizing: border-box;
    margin-right: 2px;
    text-align: right; }

.day-wrapper {
  border: 1px solid white;
  border: 1px solid black;
  grid-gap: 1px;
  background-color: black;
  width: 122px;
  height: 119px; }
  .day-wrapper:hover {
    cursor: pointer;
    border: 1px solid #565656; }

div[disabled] {
  pointer-events: none; }
  div[disabled]:hover {
    cursor: disabled; }

.monday {
  border: 1px solid #edf1f4; }
  .monday:first-child {
    grid-column: 1; }

.tuesday {
  border: 1px solid #edf1f4; }
  .tuesday:first-child {
    grid-column: 2; }

.wednesday {
  border: 1px solid #edf1f4; }
  .wednesday:first-child {
    grid-column: 3; }

.thursday {
  border: 1px solid #edf1f4; }
  .thursday:first-child {
    grid-column: 4; }

.friday {
  border: 1px solid #edf1f4; }
  .friday:first-child {
    grid-column: 5; }

.saturday {
  border: 1px solid #edf1f4; }
  .saturday:first-child {
    grid-column: 6; }

.sunday {
  border: 1px solid #edf1f4; }
  .sunday:first-child {
    grid-column: 0; }

.iconCalendar {
  width: 25px;
  margin-top: 30px; }

.summaryContainer {
  display: flex;
  flex-direction: column !important; }

#violinPlot {
  position: relative;
